import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import "../css/ContactForm.css";

function ContactForm() {
	const initialValues = {
		place: "",
		length: "",
		equipment: "",
		lessonType: "",
		multiSelect: [],
		date: "",
		name: "",
		phone: "",
		email: "",
	};

	const validationSchema = Yup.object().shape({
		place: Yup.string().required(" "),
		length: Yup.string().required(" "),
		lessonType: Yup.string().required("  "),
		equipment: Yup.string().required("  "),
		multiSelect: Yup.array().when("length", {
			is: (length) => length === "2",
			then: (schema) =>
				schema
					.length(
						1,
						"2       "
					)
					.required(
						"2       "
					),
			otherwise: (schema) =>
				schema.when("length", {
					is: (length) => length === "4",
					then: (schema) =>
						schema
							.length(
								2,
								"4       "
							)
							.required(
								"4       "
							),
				}),
		}),
		date: Yup.date()
			.required("  ")
			.nullable()
			.min(
				new Date(Date.now() + 86400000),
				"   "
			),
		name: Yup.string()
			.required(" ")
			.max(20, " 20  ")
			.matches(
				/^[a-zA-Z-\s]+$/,
				"     "
			),
		phone: Yup.string()
			.required(" ")
			.matches(
				/^(02\d{7}|[0-9]{3}\d{3,4}\d{4})$/,
				"   "
			),
		email: Yup.string()
			.email("  ")
			.required("  "),
	});

	const handleSubmit = async (values) => {
		const response = await fetch(
			process.env.REACT_APP_DISCORD_WEBHOOK_URL,
			{
				method: "POST",
				headers: {
					"Content-Type": "application/json",
				},
				body: JSON.stringify({
					content: `
                : ${values.name}
: ${values.email}
: ${values.phone}
: ${values.place}
: ${values.length}
/: ${values.equipment}
/: ${values.lessonType}
: ${values.multiSelect.join(", ")}
: ${values.date}
            `,
				}),
			}
		);

		if (response.ok) {
			alert("Form submitted successfully!");
		} else {
			const errorData = await response.json();
			console.error("Submission error:", errorData);
			alert("Form submission failed. Check console for details.");
		}
	};

	return (
		<Formik
			initialValues={initialValues}
			validationSchema={validationSchema}
			onSubmit={handleSubmit}
		>
			{() => (
				<div className="contact-form">
					<h2 style={{ paddingBottom: "1.5rem" }}></h2>
					<div className="form-container">
						<Form>
							<div className="form-group inline">
								<label>:</label>
								<Field as="select" name="place">
									<option value=""></option>
									<option value=""></option>
									<option value=""></option>
								</Field>
							</div>
							<ErrorMessage
								name="place"
								component="div"
								className="error-message"
							/>

							<div className="form-group inline">
								<label>:</label>
								<Field as="select" name="length">
									<option value=""></option>
									<option value="2">2 </option>
									<option value="4">4 </option>
								</Field>
							</div>
							<ErrorMessage
								name="length"
								component="div"
								className="error-message"
							/>

							<div className="form-group inline">
								<label>/:</label>
								<Field as="select" name="equipment">
									<option value=""></option>
									<option value=""></option>
									<option value=""></option>
								</Field>
							</div>
							<ErrorMessage
								name="equipment"
								component="div"
								className="error-message"
							/>

							<div className="form-group inline">
								<label>/:</label>
								<Field as="select" name="lessonType">
									<option value=""></option>
									<option value="">1:1 </option>
									<option value="">2:1 </option>
								</Field>
							</div>
							<ErrorMessage
								name="lessonType"
								component="div"
								className="error-message"
							/>

							<div className="form-group inline">
								<label>
									2   1,
									<br />
									4   2
								</label>
								<Field
									as="select"
									name="multiSelect"
									multiple
									className="multiSelect"
								>
									<option value="1">1</option>
									<option value="2">2</option>
									<option value="3">3</option>
									<option value="4">4</option>
									<option value="5">5</option>
								</Field>
							</div>
							<ErrorMessage
								name="multiSelect"
								component="div"
								className="error-message"
							/>

							<div className="form-group inline">
								<label htmlFor="date">:</label>
								<Field
									type="date"
									name="date"
									min={
										new Date(Date.now() + 86400000)
											.toISOString()
											.split("T")[0]
									}
								/>
							</div>
							<ErrorMessage
								name="date"
								component="div"
								className="error-message"
							/>

							<div className="form-group inline">
								<label>:</label>
								<Field type="text" name="name" />
							</div>
							<ErrorMessage
								name="name"
								component="div"
								className="error-message"
							/>

							<div className="form-group inline">
								<label>:</label>
								<Field type="text" name="phone" />
							</div>
							<ErrorMessage
								name="phone"
								component="div"
								className="error-message"
							/>

							<div className="form-group inline">
								<label>:</label>
								<Field type="email" name="email" />
							</div>
							<ErrorMessage
								name="email"
								component="div"
								className="error-message"
							/>

							<Field
								type="hidden"
								name="_subject"
								value="KGB SKI -   "
							/>

							<Field
								type="hidden"
								name="_captcha"
								value="false"
							/>

							<button type="submit"></button>
						</Form>
					</div>
				</div>
			)}
		</Formik>
	);
}

export default ContactForm;
